import React, {Component} from 'react';
import firebase from 'firebase'
import HeaderRecette from "../../components/headerRecette"
import starOn from "../../assets/images/star-on.png"
import cookTime from "../../assets/images/cook_time.png"
import chef from "../../assets/images/chef.png"
import serving from "../../assets/images/servings.png"
import "./recetteid.css"
import ReactStars from "react-rating-stars-component";
import TopbarRecettes from "../../components/TopbarRecettes";
import Loader from "../../components/Loader";
import Topbar from "../../components/TopbarRecettes";


class RecetteId extends Component {
    constructor(props){
        super();
        this.state={
            recettes:[],
            recette:""

        }
    }

    componentDidMount() {
        let id = this.props.match.params.id
        firebase.database().ref('/recettes').on('value',(snapshot)=>{

                let recettes = snapshot.val()
                 this.setState({recettes:recettes,
                 recette:recettes[id]})
        }

        )
    }

    ratingChage=(d)=>{
        console.log(d)

    }


    render() {
       let  rec = this.state.recette
        return (
            <div className="">
                <div className="header">
                <Topbar history={this.props.history}></Topbar>
                {this.state.loading === true &&
                <Loader percentage={this.state.percentage+"%"}>

                </Loader>
                }
                </div>


                <div className="wrapper ">

               <div className="container-fluid " style={{paddingBottom:100}}>
                   <div className="text-left">
                       <h1 style={{color:"#00ba4c" ,fontFamily:"cursive"}}>{rec.nomRecette}</h1>
                   </div>
                   <div className="row mt-2">
                       <div>
                           <h4>par Zairi Jawher

                           </h4>
                       </div>
                       <div>
                           <ul className="navRecette" style={{display:"inline-block"}}>


                               <li><img src={starOn}/></li>


                               <li><img src={starOn}/></li>
                               <li><img src={starOn}/></li>
                               <li><img src={starOn}/></li>
                               <li><img src={starOn}/></li>


                               <li className="votes">(80 votes)</li>
                           </ul>
                       </div>



                   </div>
                   <div className="mt-2">
                       <div className="row">
                           <div className="col-md-auto">
                               <div className="row align-items-center">

                                       <img  src={cookTime}/>


                                       <div style={{marginLeft:5}}>

                                           <div>

                                           <text> Preparation : {rec.Duree_prepa_repas}</text>
                                           </div>
                                           <div>
                                               <text>
                                                   Cuisson: {rec.Duree_Cuission}
                                               </text>

                                           </div>
                                       </div>



                               </div>

                           </div>
                           <div className="col-md-auto ml-3">
                               <div className="row align-items-center">

                                   <img  src={chef}/>


                                   <div style={{marginLeft:5}}>

                                       <div>

                                           <text> Facile</text>
                                       </div>

                                   </div>



                               </div>

                           </div>
                           <div className="col-md-auto ml-3">
                               <div className="row align-items-center">

                                   <img  src={serving}/>


                                   <div style={{marginLeft:5}}>

                                       <div>

                                           <text>{rec.Nombre_person + " " +"personnes"}</text>
                                       </div>

                                   </div>



                               </div>

                           </div>


                       </div>

                   </div>

                   <div className="row align-items-start mt-5">
                       <div className="col-md-4 text-center">
                           <img src={rec.photo} style={{width:"100%"}}/>

                           <div className="mt-3">
                               <div >
                                   <h3 style={{fontFamily:"sans-serif"}}> Je note la recette </h3>
                               </div>
                               <div  >
                                   <ReactStars
                                       classNames="ReactStars"
                                       value={5}
                                       count={5}
                                       onChange={this.ratingChage}
                                       size={30}
                                       activeColor="#ffd700"
                                   />
                               </div>
                           </div>


                       </div>
                       <div className="col-md-8">

                           <div>
                               <h3 style={{fontFamily:"cursive",color:"#00ba4c"}}>INGRDIENTS</h3>
                           </div>

                           {rec.Ingredients!=undefined && rec.Ingredients.cerealiers.map((item,key)=>(
                               <div className="mt-3">
                                   <text style={{fontSize:"1.2vw"}}>   {"- "+item.dose+" "+item.nom} </text>
                               </div>
                           ))}

                           {rec.Ingredients!=undefined && rec.Ingredients.laitiers.map((item,key)=>(
                               <div className="mt-2">
                                   <text style={{fontSize:"1.2vw"}}>   {"- "+item.dose+" "+item.nom} </text>
                               </div>
                           ))}
                           {rec.Ingredients!=undefined && rec.Ingredients.legumes.map((item,key)=>(
                               <div className="mt-2">
                                   <text style={{fontSize:"1.2vw"}}>   {"- "+item.dose+" "+item.nom} </text>
                               </div>
                           ))}

                           {rec.Ingredients!=undefined && rec.Ingredients.viandes.map((item,key)=>(
                               <div className="mt-2">
                                   <text style={{fontSize:"1.2vw"}}>   {"- "+item.dose+" "+item.nom} </text>
                               </div>
                           ))}


                           <div className="mt-3">
                               <h3 style={{fontFamily:"cursive",color:"#00ba4c"}}>PREPARATION</h3>

                           </div>




                       </div>


                   </div>
               </div>
                </div>




            </div>
        );
    }
}

export default RecetteId;
